.headerContainer {
	position: relative;
	padding-left: 30px;
	padding-right: 30px;
	&__circle {
		position: absolute;
		width: 758px;
		height: 758px;
		left: -121px;
		top: -121px;
		background: $violet;
		border-radius: 50%;
		z-index: -1; }
	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: center; }
	&__brand {
		display: flex;
		justify-content: center;
		align-items: center;
		&-logo {
			margin-right: 25px;
			height: 35px;
			text-align: center;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; } }
		&-phone {
			display: flex;
			align-items: center;
			text-align: center;
			img {
				height: 30px;
				width: 30px;
				margin-top: 5px;
				margin-right: 10px; }
			span {
				font-family: $blogger;
				font-weight: bold;
				font-size: $big-font-size;
				color: $white; } } } }
